.schedulebody{
    height: 90%;
    width: 100%;
    position: relative;


}
.schedule {
    padding: 1%;
    margin: 0;
    position: relative;
    height: 90%;
    box-sizing: border-box;
    overflow: hidden;

}
.bottom-row {
    border-bottom: 2px solid #bbbbbb;
}
.temp {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
    margin: 0;
    background-color: rgba(120, 34, 200, 0.5);
    padding:0;
}
.dash {
    border-bottom: 0.5vw dashed #666666;
}