.cal-cell{
    width: 14.28%;
    height: 100%;
    box-sizing: border-box;
    padding: 1em;
    margin: 0;
    border: 1px solid #eeeeee;
    position: relative;



}
.cal-cell-content{

    position: absolute;
    top: 5%;
    left: 5%;
    color: #555555;
}
.calendarrow {
    width: 80%;
    box-sizing: border-box;
    margin: 0;
    display: flex;
    height: 18%;
}